.section-gallery .container {

    @media screen and (min-width: $tablet) {
        display: block;
    }
    // @media screen and (min-width: $desktop) {
    //     display: block;
    // }
}


.text-gallery {
position: relative;
margin-bottom: 44px;
font-size: 11px;
line-height: 1.9;
letter-spacing: 0.1em;
text-transform: uppercase;
color: $main-text;
p {
    margin-left: 80px;
}
&>p:before {
        display: block;
        width: 60px;
        content: '';
        position: absolute;
        border-bottom: 1px solid $main-text;
        top: 25%;
        left: 0;
    }
    @media screen and (min-width: $tablet) {
        line-height: 1.36;
        &>p:before {
            top: 49%;
        }
    }
}

.list-gallery {
margin: 0 -30px;

    @media screen and (min-width: $tablet) {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px;
}}

.item-gallery {
 margin: 30px 30px 0 30px;
@media screen  and (max-width:767px){
&:first-child{
    margin-top: 0;
}}

@media screen and (min-width: $tablet) {
    margin: 30px 15px 0 15px;

&:nth-child( -n + 2){
    margin-top: 0;
}}

@media screen and (min-width: $desktop) {
    margin: 0 15px 0 15px;
}}
